import React, { Component, Fragment } from "react";
import ReactDOM from "react-dom";
import "./index.css";
import * as serviceWorker from "./serviceWorker";

import MainPage from "./mainPage";
import NavicationBar from "./NavicationBar";
import SearchPage from "./searchPage";

if (document.cookie.includes("pathName")) {
	const getPath = document.cookie.split("=")[1];
	window.history.pushState(undefined, undefined, getPath);
}

export var updateRenderState = function(v) {
	this.setState({ renderPage: v });
};

export default class Index extends Component {
	constructor() {
		super();
		this.state = {
			renderPage: [<MainPage key={Math.random() * 100} />, <NavicationBar key={Math.random() * 100} />]
		};
		updateRenderState = updateRenderState.bind(this);
	}
	componentDidMount() {
		window.addEventListener("popstate", this.checkURL);
		window.addEventListener("load", this.checkURL);
		this.checkURL();
	}

	checkURL = _ => {
		switch (window.location.pathname) {
			case "/search":
				updateRenderState(<SearchPage key={Math.random() * 100} title={this.checkHash()} />);
				window.history.pushState(undefined, undefined, "/search" + window.location.hash);
				break;
			case "/":
				updateRenderState([<MainPage key={Math.random() * 100} />, <NavicationBar key={Math.random() * 100} />]);
				break;
			default:
				break;
		}
	};

	checkHash = _ => {
		let hash;
		switch (window.location.hash) {
			case "#breakfast":
				hash = "الفطور";
				break;
			case "#lunch":
				hash = "الغداء";
				break;
			case "#dinner":
				hash = "العشاء";
				break;
			case "#snacks":
				hash = "وجبة خفيفة";
				break;
			default:
				break;
		}
		return hash;
	};
	render() {
		return <Fragment>{this.state.renderPage}</Fragment>;
	}
}

ReactDOM.render(<Index />, document.getElementById("root"));
serviceWorker.unregister();
